.App{
  text-align: center;
}

html { font-size:100%; }

@media (min-width: 640px) { body {font-size:0.8rem;} } 
@media (min-width:960px) { body {font-size:1.1rem;} } 
@media (min-width:1100px) { body {font-size:1.1rem;} }

body{
  background: #eee;
  font-family: Century Gothic;
}

.row {
  display: flex;
  width:80%;
  margin: 0 auto;
  background: #fff;
  border-bottom: 4px solid red;
}

.TotalConfirmed,
.NewConfirmed,
.NewDeaths,
.TotalDeaths,
.TotalRecovered{
  padding: 20px;
  text-align: center;
  margin: 0 auto;
  
}

.TotalConfirmed{
  color: red;
}

table{
  border-collapse: separate;
  width: 80%;
  margin: 0 auto;
  border-spacing: 0  12px;
}

table thead tr{
  background:#fff;

}

table thead tr th{
  padding: 18px 12px;
  text-align: left;

}
table tbody tr{
  background: #fff;
}

table tbody tr td{
  padding: 18px 12px;
  text-align: left;
}